* {
  box-sizing: border-box;
  margin: 0;
}

ul {
  padding: 0;
  list-style: none;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
}

.card__img {
  max-width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__id {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__rate {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  align-self: flex-end;
  font-size: 10px;
  line-height: 14px;
  text-decoration: none;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__price .text {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price .value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 100%;
  padding: 12px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  align-items: center;
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  display: block;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

body {
  font-family: Roboto, sans-serif;
}
